.page-header {
  display: flex;
  flex-direction: column;
  font-family: @main-font;
  font-size: 18px;
  line-height: 40px;
  font-weight: 700;
  text-transform: uppercase;
  background-color: @base-white;
  color: @base-black;
  margin: 0;
  padding: 0;
  position: relative;

  @media (min-width: @tablet-width) {
    flex-direction: row;
    justify-content: center;
  }
}
